// Переменные цветов
$basic-white: #ffffff;
$blue-main: #43e8ff;
$color-bgc-sidebar: #1f2339;
$color-bgc-main: #4D5474;
$info-bgc-block-item: #383e57;
$secondary-text: #a1a6be;
$color-table-border: #151e41;

// Шрифты
@font-face {
  font-family: "Open Sans";
  src: local("Open Sans"),
    url("../fonts/opensans-light.woff2") format("woff2"),
    url("../fonts/opensans-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: local("Open Sans"),
    url("../fonts/opensans-regular.woff2") format("woff2"),
    url("../fonts/opensans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: local("Open Sans"),
    url("../fonts/opensans-semibold.woff2") format("woff2"),
    url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: local("Open Sans"),
    url("../fonts/opensans-bold.woff2") format("woff2"),
    url("../fonts/opensans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

// Общие свойства
.page {
  height: 100%;
}

.page__body {
  padding: 0;
  margin: 0;
  font-family: 'Open Sans', 'Verdana', sans-serif;
  font-size: 14px;
  line-height: 19px;
  font-weight: 400;
  color: $basic-white;
  height: 100%;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-main {
  display: flex;
  flex-wrap: wrap;
  height: inherit;
}

// sidebar
.sidebar {
  background-color: $color-bgc-sidebar;
  width: 241px;
  padding-top: 31px;
  padding-left: 17px;
  padding-right: 17px;
  min-height: inherit;
  box-sizing: border-box;
}

.main-nav {
  min-height: inherit;
}

.main-nav__user-name {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-bottom: 85px;
}

.main-nav__user-name-item:not(:last-child) {
  margin-bottom: 25px;
}

.main-nav__user-name-link {
  color: $basic-white;

  &--main {
    color: $blue-main;
  }
}

.main-nav__menu {
  padding: 0;
  margin: 0;
  list-style: none;
}

.main-nav__menu-item:not(:last-child) {
  margin-bottom: 25px;
}

.main-nav__menu-link {
  color: $basic-white;
}

// info
.info {
  background-color: $color-bgc-main;
  flex-grow: 1;
}

.info__wrapper {
  margin: 0 auto;
  width: 1199px;
  padding-left: 40px;
  padding-right: 79px;
  box-sizing: border-box;
}

.info__choice-name {
  display: flex;
  justify-content: space-between;
  margin-top: 55px;
  margin-bottom: 49px;
}

.info__choice-name h1 {
  margin: 0;
  font-size: 20px;
  line-height: 27px;
  font-weight: 700;
}

.info__menu-item:not(:last-child) {
  margin-right: 23px;
}

.info__menu-item a {
  color: $basic-white;
}

.info__menu-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style: none;
}

.info__blocks {
  padding: 0;
  margin: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 340px);
  gap: 30px;
}

.info__block-item {
  min-height: 230px;
  background-color: $info-bgc-block-item;
  border-radius: 18px;
  padding: 25px 20px;
  box-sizing: border-box;
}

.info__block-temperature {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.info__block-temperature-weather {
  margin: 0;
  font-weight: 300;
  line-height: 109px;
  font-size: 80px;
  letter-spacing: -0.05em;
}

.info__block-temperature-weather sup {
  line-height: 49px;
  font-size: 36px;
}

.info__block-temperature-more-detaled-1 {
  margin-bottom: 6px;
}

.info__block-temperature-more-detaled-1,
.info__block-temperature-more-detaled-2 {
  color: $secondary-text;
  display: flex;
  // justify-content: space-between;
}

.info__block-temperature-more-detaled-1 p,
.info__block-temperature-more-detaled-2 p {
  margin: 0;
  width: 228px;
}

.info__block-item h2 {
  font-size: 16px;
  line-height: 26px;
  font-weight: 700;
  margin: 0;
}

.info__block-date {
  margin-right: 39px;
}

.info__block-date span {
  display: block;
}

.info__block-item--fields {
  background-image: url('../img/background/fields.png');
  background-repeat: no-repeat;
  background-position: 129px 46px;
}

.info__block-item--harvest {
  background-image: url('../img/background/map.png');
  background-repeat: no-repeat;
  background-position: 40px 53px;
}

.info__block-item--harvest p {
  margin: 0;
  margin-top: 7px;
}

.info__block-item--reports {
  grid-column: 1 / 3;
  grid-row: 2 / 3;
  background-image: url('../img/background/chart.png');
  background-repeat: no-repeat;
  background-position: 497px 21px;
}

.info__blog-table-statistic {
  border-collapse: collapse;
  width: 374px;
}

.info__blog-table-statistic th {
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  padding: 10px;
  box-sizing: border-box;
}

.info__block-table-title {
  font-size: 16px;
  line-height: 26px;
  font-weight: 700;
  text-align: left;
}

.info__blog-table-statistic td {
  border: 1px solid $color-table-border;
  padding: 10px;
  box-sizing: border-box;
}

.info__block-table-first-row td:first-child {
  border-left: none;
}

.info__block-table-first-row td:last-child {
  border-right: none;
}

.info__block-table-second-row td:first-child {
  border-left: none;
}

.info__block-table-second-row td:last-child {
  border-right: none;
}

.info__block-table-third-row td:first-child {
  border-left: none;
}

.info__block-table-third-row td:last-child {
  border-right: none;
}

.info__block-table-third-row td {
  border-bottom: none;
}

.info__block-staff-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.info__block-staff {
  margin: 0;
}

.info__block-staff dd {
  color: $secondary-text;
  margin: 0;
}

.info__block-staff dd {
  margin-bottom: 11px;
}

.info__block-item--staff h2 {
  margin-bottom: 10px;
}

.info__block-staff-contacts {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.info__block-staff-item:first-child {
  margin-bottom: 31+px;
}

.info__block-staff-contacts a {
  color: $basic-white;
}

.info__block-see-more-link {
  display: block;
  font-weight: 600;
  color: $blue-main;
  margin-top: 10px;
}

.info__block-item--diagram {
  width: 525px;
  min-height: 270px;
  box-sizing: border-box;

  background-image: url('../img/background/diagram.png');
  background-repeat: no-repeat;
  background-position: 246px 47px;
}

.info__block-statistic-years {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.info__block-statistic-list {
  margin: 0;
  padding: 0;
  list-style: none;

  p {
    margin: 0;
  }
}

.info__block-statistic-item {
  margin-top: 22px;
}

.info__block-statistic-item p {
  color: $secondary-text;
}

.info__block-statistic-item-text {
  margin-top: 10px;
}

.info__block-item--technic {
  width: 525px;
  min-height: 270px;
  box-sizing: border-box;
  grid-column: 3 / 4;
  margin-left: -181px;

  a {
    font-weight: 600;
    color: $blue-main;
  }
}

.info__block-technic-item {
  font-weight: 700;
}

.info__block-technic-item p {
  font-weight: 400;
  color: $secondary-text;
}
